import common from "../../common/common.js"
export default {
	data() {
		return {
			globalUser: {}
		}
	},
	methods: {
		modifySex(){
			uni.navigateTo({
				url:"../sex/sex"
			})
		},
		modifyBirthday(){
			uni.navigateTo({
				url:"../meBirthday/meBirthday"
			})
		},
		modifyNickName(){
			uni.navigateTo({
				url:"../meNickName/meNickName"
			})
		},
		operator(){
			var me = this;
			var globalUser = me.getGlobalUser("globalUser")
			uni.showActionSheet({
				itemList:["查看我的头像","从相册选择上传"],
				success(res) {
					var index = res.tapIndex;
					if(index == 0){
						//预览头像
						var faceArray = [];
						faceArray.push(globalUser.faceImage)
						uni.previewImage({
							urls:faceArray,
							current:faceArray[0]
						})
					}else if(index == 1){
						uni.chooseImage({
							count:1,
							sizeType:["compressed"],
							sourceType:["album"],
							success(res) {
								//获得临时路径
								var tempFilePaths = res.tempFilePaths[0];
								uni.navigateTo({
									url: "../meFace/meFace?tempFilePaths="+tempFilePaths
								});
							}
						})
					}
				}
			})
		},
		cleanStorage(){
			uni.clearStorageSync()
			uni.showToast({
				title:"清理缓存成功",
				mask:false,
				duration:1500
			})
		},
		logout(){
			var me = this;
			var globalUser = this.getGlobalUser("globalUser")
			uni.request({
				url:  common.serverUrl + `/user/logout?userId=${globalUser.id}&qq=${me.qq}`,
				method: "POST",
				success: (res) => {
					if (res.data.status == 200) {
						// 用户在服务端退出登录
						uni.removeStorageSync("globalUser");
						uni.switchTab({
							url:"../me/me"
						})
					}
				}
			});
		}
	},
	onShow() {
		var me = this;
		var globalUser =  me.getGlobalUser("globalUser")
		me.globalUser = globalUser;
	}
}
